import React from 'react';
import PropTypes from 'prop-types';
import { compose } from 'recompose';
import withStyles from '@material-ui/core/styles/withStyles';
import { withTranslation } from 'react-i18next';
import IconButton from '@material-ui/core/IconButton';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import { getMedia, openMedia } from '../../../Utils/Message';
import { loadMessageContents } from '../../../Utils/File';
import FileStore from '../../../Stores/FileStore';
import MessageStore from '../../../Stores/MessageStore';
import TdLibController from '../../../Controllers/TdLibController';
import './SharedDocuments.css';
import SharedDocument from '../../Tile/SharedMedia/SharedDocument';

const styles = {
    headerBackButton: {
        margin: '8px -2px 8px 12px'
    }
};

function SharedDocumentHeader({ classes, title, onClose }) {
    return (
        <div className='header-master'>
            <IconButton className={classes.headerBackButton} onClick={onClose}>
                <ArrowBackIcon />
            </IconButton>
            <div className='header-status grow cursor-pointer'>
                <span className='header-status-content'>{title}</span>
            </div>
        </div>
    );
}

class SharedDocuments extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            items: []
        };
    }

    componentDidMount() {
        const { chatId } = this.props;

        TdLibController.send({
            '@type': 'searchChatMessages',
            chat_id: chatId,
            query: '',
            sender_user_id: 0,
            from_message_id: 0,
            offset: 0,
            limit: 30,
            filter: { '@type': 'searchMessagesFilterDocument' }
        }).then(result => {
            MessageStore.setItems(result.messages);

            const store = FileStore.getStore();
            loadMessageContents(store, result.messages);

            this.setState({ items: result.messages });
        });
    }

    render() {
        const { classes, t, onClose } = this.props;
        const { items } = this.state;

        const messages = items.map(x => (<SharedDocument key={`chat_id=${x.chat_id}_message_id=${x.id}`} chatId={x.chat_id} messageId={x.id} />));

        return (
            <>
                <SharedDocumentHeader classes={classes} title={t('DocumentsTitle')} onClose={onClose}/>
                <div className='shared-documents-list'>
                    {messages}
                </div>
            </>
        );
    }
}

SharedDocuments.propTypes = {
    chatId: PropTypes.number.isRequired,
    onClose: PropTypes.func.isRequired,
    popup: PropTypes.bool,
    minHeight: PropTypes.number
};

SharedDocuments.defaultProps = {
    popup: false,
    minHeight: 0
};

const enhance = compose(
    withStyles(styles, { withTheme: true }),
    withTranslation()
);

export default enhance(SharedDocuments);